import React from 'react';
import {Button, Intent, Popover, PopoverInteractionKind, Position} from '@blueprintjs/core';
import {withRouter} from 'react-router-dom';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {deleteApplication, editApplication, loadApplications} from '../../../../actions/MachineApplicationsActions';
import _isEqual from 'lodash/isEqual';
import {DELETE, EDIT} from "../../../../api/constants/Actions";
import _find from 'lodash/find';
import {APPLICATION} from "../../../../api/constants/Entities";
import {isFormValid} from "../../../../helpers";
import {DELETE_MACHINE_APPLICATION, EDIT_MACHINE_APPLICATION} from "../../../../helpers/constants";
import Toaster from "../../../Common/Toaster";
import Can from "../../../../helpers/Can";
import { SETTINGS } from '../../../../api/constants/Pages';

class View extends React.Component{

    state = {
        application: {},
        applicationBuffer: {},
        applicationRaw: {},
        confirmDelete: false
    };

    static getDerivedStateFromProps(nextProps, prevState){
        const {applicationRaw: oldApplicationRaw} = prevState;
        const {applications, match: {params: {id}}} = nextProps;
        let applicationRaw = _find(applications, {id: Number.parseInt(id)}) || {};
        const application = {...applicationRaw};

        if(_isEqual(oldApplicationRaw, applicationRaw))
            return null;

        return {application, applicationBuffer: {...application}, applicationRaw};
    }

    componentDidUpdate(prevProps){
        const {OPERATION_SUCCESSFUL: OLD_OPERATION_SUCCESSFUL, OPERATION_FAILED: OLD_OPERATION_FAILED} = prevProps;
        const {OPERATION_SUCCESSFUL, OPERATION_FAILED, history, loadApplications, authUser: {token}} = this.props;

        if(!_isEqual(OLD_OPERATION_SUCCESSFUL, OPERATION_SUCCESSFUL)){
            const application = this.state.application.name.toUpperCase();

            loadApplications(token, {});

            if(OPERATION_SUCCESSFUL.action === EDIT_MACHINE_APPLICATION){
                history.goBack();
                Toaster.show({
                    message: `${application} Successfully Updated ðŸ˜ƒ`,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }

            if(OPERATION_SUCCESSFUL.action === DELETE_MACHINE_APPLICATION){
                history.goBack();
                Toaster.show({
                    message: `${application} Successfully Deleted ðŸ˜ƒ`,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }
        }

        if(!_isEqual(OLD_OPERATION_FAILED, OPERATION_FAILED)){
            const application = this.state.application.name.toUpperCase();
            if(OPERATION_FAILED.action === EDIT_MACHINE_APPLICATION){
                console.log(OPERATION_FAILED.data);
                Toaster.show({
                    message: `Could Not Update ${application} ðŸ˜ž`,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
            if(OPERATION_FAILED.action === DELETE_MACHINE_APPLICATION){
                console.log(OPERATION_FAILED.data);
                Toaster.show({
                    message: `Could Not Delete ${application} ðŸ˜ž`,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
        }
    }

    _handleInputChange = (name, value) => {
        this.setState(() => {
            return {
                application: {...this.state.application, [name]: value}
            }
        });
    }

    _handleDeleteApplication = () => {
        const {authUser: {token}, deleteApplication} = this.props;
        deleteApplication(token, this.state.application.id);
        this.setState({confirmDelete: false});
    }

    _handleEditApplication = () => {
        const {authUser: {token}, editApplication} = this.props;
        const {application: {name, display, path}, applicationRaw: {id}} = this.state;

        let data = new FormData();

        data.append('name', name);
        data.append('display', display);
        data.append('path', path);

        editApplication(token, id, data);
    }

    _isFormValid = () => {
        const {application: {name, display, path}} = this.state;

        let required = {name, display, path};

        return isFormValid(required);
    }

    render(){
        const {history, editingApplication, deletingApplication, authUser} = this.props;
        const {application: {name, display, path}, applicationBuffer, application, confirmDelete, applicationRaw} = this.state;
        const disableSaveButton = !this._isFormValid() || _isEqual(application, applicationBuffer);
        const applicationNotFound = !applicationRaw.id;

        const editActionAllowed = authUser.root ? true : Can.User(authUser).perform(EDIT, APPLICATION, SETTINGS);
        const deleteActionAllowed = authUser.root ? true : Can.User(authUser).perform(DELETE, APPLICATION, SETTINGS);

        return [
            <div className="bp3-dialog-body">

                <label className="bp3-label">
                    Name
                    <input
                        value={name || ''}
                        name={'name'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
                <label className="bp3-label">
                    Display Name
                    <input
                        value={display || ''}
                        name={'display'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
                <label className="bp3-label">
                    Full Path
                    <input
                        value={path || ''}
                        name={'path'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
            </div>,
            <div className="bp3-dialog-footer">
                <div className="bp3-dialog-footer-actions">
                    {applicationNotFound && (
                        <Button
                            intent={Intent.PRIMARY}
                            onClick={() => history.goBack()}
                            icon="undo"
                            text="Go Back"
                        />
                    )}

                    <Popover isOpen={this.state.confirmDelete} interactionKind={PopoverInteractionKind.CLICK} position={Position.BOTTOM}>
                        <Button
                            disabled={deletingApplication || editingApplication || applicationNotFound || !deleteActionAllowed}
                            loading={deletingApplication}
                            onClick={() => this.setState({confirmDelete: true})}
                            intent={Intent.DANGER}
                            text={'delete'}
                            icon="trash" />
                        <div style={{ padding: ".5rem", display: "flex", flexDirection: "column" }}>
                            <header>Are you sure?</header>
                            <div style={{ display: "flex", justifyContent: "space-evenly", marginTop: ".5rem" }}>
                                <Button onClick={this._handleDeleteApplication} small text="yes" intent={Intent.SUCCESS}/>
                                <Button onClick={() => this.setState({confirmDelete: false})} small text="no" intent={Intent.DANGER}/>
                            </div>
                        </div>
                    </Popover>
                    
                    <Button
                        disabled={disableSaveButton || deletingApplication || applicationNotFound || !editActionAllowed}
                        loading={editingApplication}
                        intent={Intent.PRIMARY}
                        onClick={this._handleEditApplication}
                        icon="tick"
                        text="save"
                    />
                </div>
            </div>
        ];
    }

    static propTypes = {
        history: PropTypes.object.isRequired,
        authUser: PropTypes.object.isRequired,
        match: PropTypes.object.isRequired,
        OPERATION_FAILED: PropTypes.object.isRequired,
        OPERATION_SUCCESSFUL: PropTypes.object.isRequired,
        editApplication: PropTypes.func.isRequired,
        deleteApplication: PropTypes.func.isRequired,
        loadApplications: PropTypes.func.isRequired,
        editingApplication: PropTypes.bool.isRequired,
        deletingApplication: PropTypes.bool.isRequired,
        applications: PropTypes.array.isRequired,
    };
}

const mapStateToProps = (
    {authUser, OPERATION_FAILED, OPERATION_SUCCESSFUL, editingApplication, deletingApplication, applications}) => (
        {authUser, OPERATION_FAILED, OPERATION_SUCCESSFUL, editingApplication, deletingApplication, applications});
const mapDispatchToProps = dispatch => bindActionCreators({deleteApplication, editApplication, loadApplications}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(View));


